import "./faqs.scss"

const Faqs = ()=>{
    return(
        <section className="faq">
            <div className="container">
                <div className="heading text-center">
                    <h2 className="font_main">الاسئله الشائعه</h2>
                    <div className="border"></div>
                    <p className="font_child">الدعم الفني هو خدمة تقدم المساعدة للطلاب عند الحاجة ، في أي مكان وفي أي وقت.</p>
                </div>

                  <div className="tab-content" id="nav-tabContent">

                    <div className="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                        <div className="accordion" id="accordionExample">
                            <div className="card">
                            <div className="card-header" id="headingOne">
                                <h2 className="mb-0">
                                <button className="btn btn-link btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                    <span className="font_child"># 1 ما هو موقع دورات؟</span>							  
                                </button>
                                </h2>
                            </div>
                            <div id="collapseOne" className="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
                                <div className="card-body font_child">
                                    هو موقع خاص لتقديم كورسات متعددة فى مجالات كثيرة .
                                </div>
                            </div>
                            </div>
                            <div className="card">
                            <div className="card-header" id="headingTwo">
                                <h2 className="mb-0">
                                <button className="btn btn-link btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="true" aria-controls="collapseTwo">
                                    <span className="font_child"># 2 كيف يعمل التطبيق؟</span>							  
                                </button>
                                </h2>
                            </div>
                            <div id="collapseTwo" className="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
                                <div className="card-body font_child">
                                    ينقسم التطبيق الى جزئين الجزء الأول يتم التعامل معه بدون التسجيل يتم من خلاله التعرف على التطبيق والجزء الثانى يجب امتلاك اميل للأستفادة من الخدمات التى يخدمها التطبيق.
                                </div>
                            </div>
                            </div>
                            <div className="card">
                            <div className="card-header" id="headingThree">
                                <h2 className="mb-0">
                                <button className="btn btn-link btn-block text-left collapsed" type="button" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                                    <span className="font_child"># 3 ما هى مميزات التطبيق؟</span>							  
                                </button>
                                </h2>
                            </div>
                            <div id="collapseThree" className="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
                                <div className="card-body font_child">
                                    <ol>
                                        <li>التقديم على حجز كورسات.</li>
                                        <li>مصاريف الدورات بسيطة جدا.</li>
                                        <li>تقديم العديد من الشكاوى والبلاغات:</li>
                                        <li>تقديم الدعم والتسهيل لجميع المستخدمين.</li>
                                    </ol>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>
                
                </div>
            </div>
        </section>

    )
}

export default Faqs;